/** @jsxImportSource @emotion/react */
import React from 'react'
import { atom, useRecoilState } from 'recoil'
import MuiButton from '@mui/material/Button'

import { Project } from '../../types'
import { AccordionPanel } from '../AccordionPanel'
import { Title } from '../Title'
import { ProjectSamples } from './ProjectSamples'
import { ProjectSitesMapPreview } from './ProjectSitesMapPreview'
import { ReadOnlyField } from '../ReadOnlyField'
import { formatUtcDateTimeToAppDate } from '../../lib/formats'

export interface ProjectDetailProps {
  project: Project
}

const isExpandedState = atom<boolean>({
  key: 'ProjectDetail/isExpandedState',
  default: true,
})

export const ProjectDetail = ({ project }: ProjectDetailProps): JSX.Element => {
  const [isPanelExpanded, setIsPanelExpanded] = useRecoilState(isExpandedState)
  const isEditing = false

  return (
    <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <AccordionPanel
        value={isPanelExpanded}
        onChange={setIsPanelExpanded}
        locked={isEditing}
        summary={
          <Title>
            Project {project.name} <Title.Subtitle>({project.sampleCount} samples)</Title.Subtitle>
          </Title>
        }
        actions={
          <MuiButton disabled variant="outlined">
            Edit
          </MuiButton>
        }
      >
        <div
          css={{
            flex: 1,
            display: 'grid',
            gap: '0.75em',
            gridTemplateColumns: 'auto 1fr auto',
            gridTemplateAreas: `
              "id             modelCount    map"
              "createdDate    description   map"
              "contactName    .             map"
              ".              .             map"
            `,
          }}
        >
          <ReadOnlyField gridArea="id" value={project.id} label="Project ID" />
          <ReadOnlyField
            gridArea="createdDate"
            value={formatUtcDateTimeToAppDate(project.createdDate ?? null)}
            label="Created On"
          />
          <ReadOnlyField gridArea="contactName" value={project.contactName} label="Contact Name" />
          <ReadOnlyField gridArea="modelCount" value={project.modelCount} label="# Models" />
          <ReadOnlyField gridArea="description" value={project.description} label="Description" />
          <div css={{ gridArea: 'map' }}>
            <ProjectSitesMapPreview projectId={project.id} projectName={project.name} />
          </div>
        </div>
      </AccordionPanel>
      <ProjectSamples projectId={project.id} projectName={project.name} />
    </div>
  )
}
