import gql from 'graphql-tag'

import { Api, ApiError } from '../api'
import { InvalidateFn } from '../selectors'

interface RemoveProjectSamplesInput {
  projectId: number
  sampleIds: number[]
}

export const removeProjectSamplesMutation = gql`
  mutation ($projectId: Int!, $sampleIds: [Int]) {
    removeProjectSamples(projectId: $projectId, sampleIds: $sampleIds)
  }
`

export const removeProjectSamples =
  (api: Api, invalidate: InvalidateFn) =>
  async (params: RemoveProjectSamplesInput): Promise<true> => {
    const data = await api.mutation<{
      removeProjectSamples: number // NOTE: what is return type?
    }>(removeProjectSamplesMutation, { ...params })

    // if (!data.removeProjectSamples) throw new ApiError('project samples did not update') // TODO: enable once endpoint supports

    invalidate('projectSamples')
    return true
  }
