/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiButton from '@mui/material/Button'
import AddCircleOutlineIcon from '@mui/icons-material/AddCircle'
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircle'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'

import { withSuspense } from '../withSuspense'
import { selectProjectSamplesByProjectId } from '../../data/selectors'
import { SamplesDataGrid } from '../SamplesDataGrid'
import { DataGridRecordCount } from '../DataGrid'
import { ModifyProjectSamples, ModifyProjectSamplesProps } from './ModifyProjectSamples'

interface ProjectDetailProps {
  projectId: number
  projectName: string
}

export const ProjectSamples = withSuspense(
  ({ projectId, projectName }: ProjectDetailProps): JSX.Element => {
    const samples = useRecoilValue(selectProjectSamplesByProjectId(projectId))
    const [isOperating, setIsOperating] = useState(false)
    const [modifyMode, setModifyMode] = useState<ModifyProjectSamplesProps['initialMode']>(null)

    const handleClose = () => {
      if (isOperating) return
      setModifyMode(null)
    }

    return (
      <>
        <div css={{ minHeight: '2em', display: 'flex', alignItems: 'center', gap: '0.5rem' }}>
          <div css={{ fontWeight: '500', marginLeft: '1em', marginRight: 'auto' }}>Samples</div>
          <MuiButton
            css={{ width: '6em' }}
            // startIcon={<AddCircleOutlineIcon />}
            variant="outlined"
            onClick={() => {
              setModifyMode('add')
            }}
          >
            Add
          </MuiButton>
          <MuiButton
            css={{ width: '6em' }}
            // startIcon={<RemoveCircleOutlineIcon />}
            variant="outlined"
            disabled={samples.length === 0}
            onClick={() => {
              setModifyMode('remove')
            }}
          >
            Remove
          </MuiButton>
          <DataGridRecordCount total={samples.length} visible={samples.length} descriptor="sample" />
        </div>
        <div css={{ flex: 1, minHeight: '10em' }}>
          <SamplesDataGrid
            samples={samples}
            sitesListingSource={{ type: 'project', id: projectId, name: projectName }}
          />
        </div>
        <MuiDialog open={!!modifyMode} fullWidth maxWidth="md" onClose={handleClose}>
          <MuiDialogContent>
            <ModifyProjectSamples
              initialMode={modifyMode}
              projectId={projectId}
              projectName={projectName}
              samples={samples}
              onOperatingChange={setIsOperating}
            />
          </MuiDialogContent>
          <MuiDialogActions>
            <MuiButton onClick={handleClose} disabled={isOperating}>
              Close
            </MuiButton>
          </MuiDialogActions>
        </MuiDialog>
      </>
    )
  },
  () => <MuiSkeleton css={{ marginTop: '1rem' }} height="10rem" variant="rectangular" />
)
