import React from 'react'
import { useRecoilValue } from 'recoil'

import { selectProjectById } from '../../data/selectors'
import { withSuspense } from '../withSuspense'
import { ProjectDetail as Component } from './ProjectDetail'
import { Fallback } from './Fallback'

export interface ProjectDetailProps {
  projectId: string | number
}

export const ProjectDetail = withSuspense(({ projectId }: ProjectDetailProps): JSX.Element => {
  const project = useRecoilValue(selectProjectById(Number(projectId)))

  if (project === null) return <h2>Project {projectId} not found.</h2>

  return <Component project={project} />
}, Fallback)
